.balance-navigation-bar {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 28px;
  gap: 0;
  background: linear-gradient(135deg, #f9fafc 60%, #e3e9f6 100%);
  border-radius: 14px;
  box-shadow: 0 4px 16px rgba(52, 98, 219, 0.06);
  border: 1px solid #e0e6ed;
  padding: 8px;
  overflow: hidden;
  max-width: 520px;
  min-width: 200px;
}

.tab {
  padding: 8px 12px;
  cursor: pointer;
  font-size: 1.08rem;
  font-weight: 600;
  border: none;
  background: none;
  color: #3462db;
  border-radius: 6px;
  transition: background 0.15s, color 0.15s;
  position: relative;
  outline: none;
  margin: 0 2px;
}

.tab.active {
  background: #e3e9f6;
  color: #1a3a7c;
}

.tab:hover:not(.active) {
  background: #e3e9f6;
  color: #1a3a7c;
}